body {
    background-color: navy;
    margin: 0;
}

.nav {
    display: flex;
    justify-content: center;

    .nav-img {
        background-color: lightslategrey;
        border-radius: 30px;
        display: flex;
        align-items: center;
        height: 100px;

        h1 {
            color: aliceblue;
            margin: 1rem;
        }

        img {
            height: 100%;
            border-radius: 30px;
            cursor: pointer;
        }
    }
}

.movie-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;


    .movie-item {
        text-align: center;
        width: 15rem;
        height: 25rem;
        margin: 1.2rem;
        color: white;
        background-color: rgb(60, 57, 161);
        border-radius: 1rem;


        img {
            width: 100%;
            height: 90%;
            transition: all 0.8s;
            cursor: pointer;

            &:hover {
                scale: 0.7;
                border-radius: 10rem;
            }
        }
    }


    .movie-info {
        display: flex;
        justify-content: space-evenly;
        font-size: 1.1rem;
        background-color: lightcoral;
        border-radius: 10px;


        h4 {
            margin: 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        span {
            color: yellow;
        }
    }
}

.detail-container {
    display: flex;
    width: 80%;
    margin: 1rem auto;
    max-height: 100%;
    gap: 10rem;
    border-radius: 1rem;
    background-color: rgb(60, 57, 161);

    .detail-img {
        width: 35%;
        max-width: 35rem;
        max-height: 35rem;

        img {
            width: 100%;
            height: 100%;
            border-radius: 1rem;
        }
    }

    .detail-info {
        width: 50%;

        .detail-title {
            font-size: 1.2rem;
            font-weight: bold;
            color: #fff;

            h3 {
                color: coral;
            }
        }

        .detail-overview {
            color: #c3bdbd;
        }

        .detail-stats {
            display: flex;
            justify-content: space-between;

            h3 {
                color: lightskyblue;
            }

            .detail-stat {
                color: antiquewhite;
                font-weight: bold;
            }
        }
    }
}



@media screen and (max-width: 1060px) {
    .detail-container {
        display: block;
        width: 90%;

        .detail-img {
            width: 80%;
            margin: 0 auto;

            img {
                max-width: 40rem;
                max-height: 30rem;
            }
        }

        .detail-info {
            width: 100%;
            text-align: center;

            .detail-title {
                font-size: 0.6rem;
                font-weight: bold;
                color: #fff;

                h3 {
                    color: coral;
                    font-size: 1rem;
                }
            }

            .detail-stats {
                display: block;
            }
        }
    }
}